  left: 0;
}
.swiper .swiper-button-prev::after {
  content: "";
  top: calc(50% - 10px);
  left: calc(50% - 10px);
  width: 20px;
  height: 20px;
  background: center center no-repeat url("../img/arrowpromo.svg") transparent;
}
.swiper .swiper-button-next {
  right: 0;
  transform: scale(-1, 1);
}
